import Vue from "vue";
import {
  ValidationProvider,
  extend,
  localize,
  ValidationObserver
} from "vee-validate";
import { email, alpha_num, required, regex } from "vee-validate/dist/rules";

import TW from "vee-validate/dist/locale/zh_TW.json";

// extend("secret", {
//   validate: value => value === "example",
//   message: "This is not the magic word"
// });
extend("email", { ...email, message: "" });
extend("required", { ...required, message: "" });
extend("alpha_num", { ...alpha_num, message: "" });
extend("regex", { ...regex, message: " 2 ~ 3  1 " });
// extend("decimal", { ...decimal, message: "test" });

extend("minmax", {
  validate(value, { min, max }) {
    return value.length >= min && value.length <= max;
  },
  message: " 6 ~ 12 ",
  params: ["min", "max"]
});

extend("nameMinMax", {
  validate(value, { min, max }) {
    return value.length >= min && value.length <= max;
  },
  message: " 1 ~ 10 ",
  params: ["min", "max"]
});

extend("confirmedBy", {
  params: ["target"],
  // Target here is the value of the target field
  validate(value, { target }) {
    return value === target;
  },
  // here it is its name, because we are generating a message
  message: ""
});

localize("zh_TW", TW);

Vue.component("ValidationProvider", ValidationProvider);
Vue.component("ValidationObserver", ValidationObserver);
